import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import Topnav from '../components/Topnav';
import './Signup.css';

//  API URL 
const API_BASE_URL = 'https://re-local.onrender.com';

const COUNTRIES = ['Korea','USA','Japan','China','Germany','France','Canada','UK','Spain','Australia'];
const LANGUAGES = ['Korean','English','Japanese','Chinese','German','French','Spanish','Portuguese','Russian','Arabic'];

//  10
const TAGS = [
  'K-pop','Street Food','Museum','Hanok','Hiking',
  'Night Market','Cafe Tour','Theater','Festival','History'
];

export default function Signup() {
  //  
  const [name, setName] = useState('');
  const [gender, setGender] = useState('');      // "0" | "1"
  const [userid, setUserid] = useState('');
  const [password, setPassword] = useState('');

  // 
  const [country, setCountry] = useState('Korea');
  const [language, setLanguage] = useState('Korean');

  //  ( )    
  const [selectedTags, setSelectedTags] = useState([]);

  // UX
  const [loading, setLoading] = useState(false);
  const [errMsg, setErrMsg] = useState('');
  const navigate = useNavigate();

  //   
  const isValidGender = gender === '0' || gender === '1';

  const canSubmit =
    name.trim() &&
    userid.trim() &&
    password &&
    isValidGender &&
    country &&
    language &&
    selectedTags.length > 0 &&
    !loading;

  //        
  const toggleTag = (tag) => {
    setSelectedTags(prev =>
      prev.includes(tag) ? prev.filter(t => t !== tag) : [...prev, tag]
    );
  };

  const onSubmit = async (e) => {
    e.preventDefault();
    if (!canSubmit) return;

    const payload = {
      name: name.trim(),
      gender: Number(gender),
      userid: userid.trim(),
      password,
      country,
      language,
      interestTag: selectedTags.map(tag => `#${tag}`).join(','),   // #tag1,#tag2  
    };

    try {
      setLoading(true);
      setErrMsg('');
      
      //    
      const res = await axios.post(`${API_BASE_URL}/api/users/signup`, payload, {
        headers: { 'Content-Type': 'application/json' },
        withCredentials: false,
      });
      
      console.log(' :', res.data);
      alert(' !');
      navigate('/login');
    } catch (err) {
      console.error(' :', err);
      
      //     
      if (err.response) {
        //     
        setErrMsg(err.response.data?.message || ` : ${err.response.status}`);
      } else if (err.request) {
        //      ( )
        setErrMsg('   .    .');
      } else {
        //  
        setErrMsg('   .     .');
      }
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="signup-container">
      <Topnav />
      
      <div className="signup-form-container">
        <div className="signup-header">
          <h1 className="signup-title"></h1>
          <p className="signup-subtitle">KurtainCall  </p>
        </div>

        <form className="signup-form" onSubmit={onSubmit}>
          {errMsg && (
            <div className="error-message">
              {errMsg}
            </div>
          )}

          {/*  */}
          <div className="form-group">
            <label htmlFor="name" className="form-label"></label>
            <input
              id="name"
              type="text"
              className="form-input"
              placeholder=" "
              value={name}
              onChange={(e) => setName(e.target.value)}
            />
          </div>

          {/*  */}
          <div className="form-group">
            <label htmlFor="userid" className="form-label"></label>
            <input
              id="userid"
              type="text"
              className="form-input"
              placeholder=" "
              value={userid}
              onChange={(e) => setUserid(e.target.value)}
              autoComplete="username"
            />
          </div>

          {/*  */}
          <div className="form-group">
            <label htmlFor="password" className="form-label"></label>
            <input
              id="password"
              type="password"
              className="form-input"
              placeholder=" "
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              autoComplete="new-password"
            />
          </div>

          {/* : 0/1 */}
          <div className="form-group">
            <label htmlFor="gender" className="form-label"> (: 0, : 1)</label>
            <input
              id="gender"
              type="number"
              className="form-input"
              placeholder=": 0  1"
              value={gender}
              onChange={(e) => setGender(e.target.value)}
              min="0"
              max="1"
            />
          </div>

          {/*   */}
          <div className="form-group">
            <span className="form-label"></span>
            <div className="toggle-group">
              {COUNTRIES.map((c) => (
                <button
                  type="button"
                  key={c}
                  className={`toggle ${country === c ? 'active' : ''}`}
                  onClick={() => setCountry(c)}
                >
                  {c}
                </button>
              ))}
            </div>
          </div>

          {/*   */}
          <div className="form-group">
            <span className="form-label"></span>
            <div className="toggle-group">
              {LANGUAGES.map((l) => (
                <button
                  type="button"
                  key={l}
                  className={`toggle ${language === l ? 'active' : ''}`}
                  onClick={() => setLanguage(l)}
                >
                  {l}
                </button>
              ))}
            </div>
          </div>



          {/*  () */}
          <div className="form-group">
            <span className="form-label">  ( )</span>
            <div className="chip-group">
              {TAGS.map(tag => (
                <button
                  type="button"
                  key={tag}
                  className={`chip ${selectedTags.includes(tag) ? 'selected' : ''}`}
                  onClick={() => toggleTag(tag)}
                >
                  {tag}
                </button>
              ))}
            </div>
          </div>

          <button className="signup-btn" type="submit" disabled={!canSubmit}>
            {loading ? ' ' : ''}
          </button>
        </form>
        
        <div className="login-link">
          <p className="login-text">  ?</p>
          <button 
            type="button" 
            className="login-btn"
            onClick={() => navigate('/login')}
          >
            
          </button>
        </div>
      </div>
    </div>
  );
}
