import React, { useState, useEffect } from 'react';
import Topnav from './Topnav';
import './AITranslation.css';
const API_BASE = 'https://re-local.onrender.com';
  
const AITranslation = () => {

  const [isListening, setIsListening] = useState(false);
  const [translationResult, setTranslationResult] = useState('');
  const [isProcessing, setIsProcessing] = useState(false);
  const [isSpeaking, setIsSpeaking] = useState(false);
  
  //    
  const [selectedMode, setSelectedMode] = useState('realtime'); //  
  const [inputText, setInputText] = useState(''); //  
  const [fromLanguage, setFromLanguage] = useState('ko'); //  
  const [toLanguage, setToLanguage] = useState('en'); //  
  const [ttsEnabled, setTtsEnabled] = useState(true); // TTS  (realtime '  '  )
  const [intermediateResults, setIntermediateResults] = useState({}); //  
  
  //   
  const [audioChunks, setAudioChunks] = useState([]); //    
  const [audioBlob, setAudioBlob] = useState(null);
  const [mediaRecorder, setMediaRecorder] = useState(null);
  const [isRecording, setIsRecording] = useState(false);
  
  

  // From    To     
  React.useEffect(() => {
    if (fromLanguage === 'ko') {
      setToLanguage('en');
    } else if (fromLanguage === 'en') {
      setToLanguage('ko');
    }
  }, [fromLanguage]);

  //     
  React.useEffect(() => {
    return () => {
      if (window.currentAudio) {
        window.currentAudio.pause();
        window.currentAudio = null;
      }
    };
  }, []);

    // MediaRecorder  ( )
  React.useEffect(() => {
    const initializeMediaRecorder = async () => {
      try {
        const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
        const recorder = new MediaRecorder(stream, {
          mimeType: 'audio/webm;codecs=opus'
        });
        
        //     (1)
        recorder.ondataavailable = (event) => {
          if (event.data.size > 0) {
            //   
            setAudioChunks(prev => [...prev, event.data]);
          }
        };
        
        recorder.onstop = () => {
          //       Blob 
          setAudioChunks(prev => {
            if (prev.length > 0 && selectedMode === 'realtime') {
              const finalBlob = new Blob(prev, { type: 'audio/webm;codecs=opus' });
              console.log('  Blob :', finalBlob.type, finalBlob.size, 'bytes');
              
              //    (state   )
              handleRealtimeTranslationWithBackend(finalBlob);
              
              return []; //   
            }
            return prev;
          });
        };
        
        setMediaRecorder(recorder);
      } catch (error) {
        console.error('  :', error);
        setTranslationResult('  .    .');
      }
    };
    
    initializeMediaRecorder();
  }, [selectedMode]);
  
  

    //    ( )
  const startRecording = () => {
    if (mediaRecorder && mediaRecorder.state === 'inactive') {
      //   
      setTranslationResult('');
      setIntermediateResults({});
      setAudioChunks([]); //   
      setIsRecording(true);
      setIsListening(true);
      
      //   (1  )
      mediaRecorder.start(1000);
      console.log('  -   ');
    }
  };

  //   
  const stopRecording = () => {
    if (mediaRecorder && mediaRecorder.state === 'recording') {
      mediaRecorder.stop();
      setIsRecording(false);
      setIsListening(false);
      console.log(' ');
    }
  };

     //    ( )
   const handleVoiceInput = () => {
     if (selectedMode === 'realtime') {
       //  : MediaRecorder 
       if (!isRecording) {
         startRecording();
       } else {
         stopRecording();
       }
     }
   };

  //    -   
  const handleRealtimeTranslationWithBackend = async (audioBlob) => {
    if (!audioBlob) {
      console.error('audioBlob !');
      return;
    }
    
    console.log('  :', audioBlob.type, audioBlob.size, 'bytes');
    
    setIsProcessing(true);
    setIntermediateResults(prev => ({ ...prev, stt: '   ...' }));
    
    try {
      // 1.  webm    (POST)
      const filename = await uploadAudioToBackend(audioBlob);
      setIntermediateResults(prev => ({ ...prev, stt: `   : ${filename}` }));
      
      // 2. STS() STT()  
      const sttPromise = ttsEnabled
        ? getSTTText(filename).catch((e) => {
            //  STT        
            console.info('[info] STT     404.   .', e?.message || e);
            return '';
          })
        : Promise.resolve('');

      const [translatedAudioBlob, sttText] = await Promise.all([
        getSTSResult(filename), // ( )
        sttPromise
      ]);

      // 3.   TT( )    
      if (sttText && ttsEnabled) {
        setIntermediateResults(prev => ({ ...prev, stt: `  : ${sttText}` }));
        const finalTranslatedText = await performTranslation(sttText, fromLanguage, toLanguage);
        setIntermediateResults(prev => ({ ...prev, translation: finalTranslatedText }));
        setTranslationResult(finalTranslatedText);
      } else {
        setTranslationResult('  .');
      }
      
      // 4.      ( )
      if (translatedAudioBlob) {
        await playAudioResult(translatedAudioBlob);
      }

      setIntermediateResults(prev => ({ 
        ...prev, 
        stt: sttText ? '  ' : ' ( ) ',
        tts: ' () '
      }));
    } catch (error) {
      console.error('    :', error);
      setTranslationResult('    .');
    } finally {
      setIsProcessing(false);
    }
  };

  const uploadAudioToBackend = async (audioBlob) => {
    const formData = new FormData();
    formData.append('audio', audioBlob, 'recording.webm');
  
    const res = await fetch(`${API_BASE}/api/transcribe/upload`, {
      method: 'POST',
      body: formData,
    });
    if (!res.ok) throw new Error(` : ${res.status}`);
  
    const json = await res.json();
    console.log(' ,  :', json); // { filename, mimetype, size ... }
    return json.filename;
  };
  

  //      (GET)
  const getSTSResult = async (filename) => {
    const res = await fetch(
      `${API_BASE}/api/transcribe/sts?filename=${encodeURIComponent(filename)}`,
      { headers: { Accept: 'audio/mpeg' } }
    );
    if (!res.ok) throw new Error(`    : ${res.status}`);
    return await res.blob(); // mp3 Blob
  };

  //  : STT ( )  (   ,     )
  const getSTTText = async (filename) => {
    // 1) /stt ()
    try {
      const r = await fetch(
        `${API_BASE}/api/transcribe/stt?filename=${encodeURIComponent(filename)}`,
        { headers: { Accept: 'application/json' } }
      );
      if (r.ok) {
        const j = await r.json().catch(() => ({}));
        const text = j.text || j.transcript || j.recognizedText || '';
        if (text) return text;
      } else {
        // 404    
        console.info('[info] /stt not available:', r.status);
      }
    } catch (e) {
      console.info('[info] /stt request failed, fallback to /sts json', e?.message || e);
    }

    // 2) /sts  JSON (     )
    try {
      const r2 = await fetch(
        `${API_BASE}/api/transcribe/sts?filename=${encodeURIComponent(filename)}`,
        { headers: { Accept: 'application/json' } }
      );
      if (r2.ok) {
        const j2 = await r2.json().catch(() => ({}));
        const text = j2.text || j2.sourceText || j2.stt || j2.recognizedText || '';
        if (text) return text;
      } else {
        console.info('[info] /sts json not available:', r2.status);
      }
    } catch (e) {
      console.info('[info] /sts json request failed', e?.message || e);
    }

    //      ( )
    return '';
  };
  

  //  mp3  
  const playAudioResult = async (audioBlob) => {
    try {
      //      
      if (window.currentAudio) {
        window.currentAudio.pause();
        window.currentAudio = null;
      }
      
      // Blob  URL 
      const audioUrl = URL.createObjectURL(audioBlob);
      const audio = new Audio(audioUrl);
      
      //       
      window.currentAudio = audio;

      setIsSpeaking(true);
      
      //  
      await audio.play();
      console.log('   ');
      
      //    
      audio.onended = () => {
        setIsSpeaking(false);
        URL.revokeObjectURL(audioUrl);
        window.currentAudio = null;
        console.log('   ');
      };
      
      //    
      audio.onerror = () => {
        setIsSpeaking(false);
        URL.revokeObjectURL(audioUrl);
        window.currentAudio = null;
        console.error('    ');
      };
      
    } catch (error) {
      setIsSpeaking(false);
      console.error('  :', error);
      throw error;
    }
  };

  const handleTextTranslation = async () => {
    if (!inputText.trim()) return;
    
    setIsProcessing(true);
    try {
      const translatedText = await performTranslation(inputText, fromLanguage, toLanguage);
      setTranslationResult(translatedText);
      
      if (ttsEnabled) {
        await performTTS(translatedText, toLanguage);
      }
    } catch (error) {
      console.error('  :', error);
      setTranslationResult('   .');
    } finally {
      setIsProcessing(false);
    }
  };

  



  //  API  ( )
  const performTranslation = async (text, from, to) => {
    try {
      const res = await fetch(`${API_BASE}/api/transcribe/tt`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Accept: 'application/json',
        },
        body: JSON.stringify({
          text,
          targetLang: to,
        }),
      });
  
      if (!res.ok) {
        const errBody = await res.text().catch(() => '');
        throw new Error(` : ${res.status} ${errBody}`);
      }
  
      const result = await res.json().catch(async () => {
        //   JSON     
        const fallbackText = await res.text().catch(() => '');
        return { translated: fallbackText };
      });
  
      //    
      const out =
        result.translated ?? result.translatedText ?? result.text ?? '';
  
      return out || '   .';
    } catch (err) {
      console.error(' API :', err);
      throw err; //  handleTextTranslation  
    }
  };
  

      

  // TTS API  ( )
  const performTTS = async (text, language) => {
    try {
      //   TTS API  (     )
      /*
      const response = await fetch('/api/tts', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          text: text,
          language: language
        })
      });
      
      if (!response.ok) {
        throw new Error(`TTS : ${response.status}`);
      }
      
      const result = await response.json();
      // TTS   
      const audio = new Audio(result.audioUrl);
      await audio.play();
      */
      
      // :  TTS    
      await new Promise(resolve => setTimeout(resolve, 1500));
      console.log('TTS  :', text, ':', language);
      
      //   TTS  ( TTS API  )
      if ('speechSynthesis' in window) {
        const utterance = new SpeechSynthesisUtterance(text);
        utterance.lang = language === 'ko' ? 'ko-KR' : 'en-US';
        utterance.rate = 0.9;
        speechSynthesis.speak(utterance);
      }
    } catch (error) {
      console.error('TTS API :', error);
      throw error;
    }
  };


  return (
    <div className="ai-translation-container">
      <Topnav />
      
      <div className="ai-translation-content">
        <div className="ai-translation-header">
          <h1 className="ai-translation-title">AI   </h1>
          <p className="ai-translation-subtitle">STT, , TTS    </p>
        </div>

        <div className="translation-main">
          {/*    */}
          <div className="mode-selection">
            <h3>  </h3>
            <select 
              value={selectedMode} 
              onChange={(e) => setSelectedMode(e.target.value)}
              className="mode-dropdown"
            >
              <option value="realtime"> </option>
              <option value="text">  </option>
            </select>
          </div>

          {/*   */}
          <div className="language-selection">
            <div className="language-pair">
              <div className="language-input">
                <label>From:</label>
                <select value={fromLanguage} onChange={(e) => setFromLanguage(e.target.value)}>
                  <option value="ko">KR</option>
                  <option value="en">ENG</option>
                </select>
              </div>
              <div className="language-arrow"></div>
              <div className="language-output">
                <label>To:</label>
                <select value={toLanguage} onChange={(e) => setToLanguage(e.target.value)}>
                  <option value="en">ENG</option>
                  <option value="ko">KR</option>
                </select>
              </div>
            </div>
          </div>

          {/* TTS  */}
          <div className="tts-toggle">
            <label>
              <input 
                type="checkbox" 
                checked={ttsEnabled} 
                onChange={(e) => setTtsEnabled(e.target.checked)}
              />
              {selectedMode === 'text' ? '   ' : '   '}
            </label>
          </div>

          {/*   */}
          <div className="input-section">
            {/*    -      */}
            {selectedMode !== 'realtime' && selectedMode !== 'stt' && (
              <div className="text-input-area">
                <textarea
                  value={inputText}
                  onChange={(e) => setInputText(e.target.value)}
                  placeholder="   (   )"
                  className="text-input"
                  rows="4"
                />
                <button 
                  onClick={handleTextTranslation}
                  className="translate-btn"
                  disabled={!inputText.trim() || isProcessing}
                >
                   
                </button>
              </div>
            )}

            {/*    -    */}
            {selectedMode === 'realtime' && (
              <div className="voice-input-area">
                <div className="voice-input-header">
                  <h4>  </h4>
                  <p className="voice-input-description">
                        
                  </p>
                </div>
                
                <button 
                  className={`microphone-btn ${isListening ? (isRecording ? 'recording' : 'listening') : ''}`}
                  onClick={handleVoiceInput}
                  disabled={isProcessing}
                >
                  {isRecording ? '  ...' : '  '}
                </button>
            
                {isListening && (
                  <div className="listening-status">
                    <div className="listening-animation">
                      <div className="wave"></div>
                      <div className="wave"></div>
                      <div className="wave"></div>
                    </div>
                    <p>
                       .   ...
                    </p>
                  </div>
                )}
              </div>
            )}
          </div>

          {/*    */}
          <div className="translation-results">
            <div className="result-card">
              <h3> </h3>
              
              {/*    (  ) */}
              {selectedMode === 'realtime' && Object.keys(intermediateResults).length > 0 && (
                <div className="intermediate-results">
                  <h4 className="intermediate-title">   </h4>
                  {intermediateResults.stt && (
                    <div className="result-step">
                      <span className="step-label">  :</span>
                      <p className="step-text">{intermediateResults.stt}</p>
                    </div>
                  )}
                  {intermediateResults.translation && (
                    <div className="result-step">
                      <span className="step-label">  :</span>
                      <p className="step-text">{intermediateResults.translation}</p>
                    </div>
                  )}
                </div>
              )}
              
              
              
              {/*   */}
              <div className="final-result">
                <h4>
                  {selectedMode === 'realtime' && '   '}
                  {selectedMode === 'text' && '  '}
                </h4>
                <div className="result-content">
                  {translationResult ? (
                    <p className={`result-text ${isProcessing ? 'processing' : ''}`}>
                      {translationResult}
                    </p>
                  ) : (
                    <p className="placeholder-text">
                      {selectedMode === 'realtime' && '     '}
                      {selectedMode === 'text' && '    '}
                    </p>
                  )}
                </div>
              </div>
              
              {/*   */}
              {isListening && (
                <div className="status-indicator">
                  <p className="status-text">
                      .     !
                  </p>
                </div>
              )}
              
              {isProcessing && (
                <div className="status-indicator">
                  <p className="status-text">  ...</p>
                </div>
              )}
              
              {isSpeaking && (
                <div className="status-indicator">
                  <p className="status-text">   ...</p>
                </div>
              )}
              
              {/*        */}
              {translationResult && translationResult.includes(' ') && (
                <div className="permission-help">
                  <button 
                    className="permission-btn"
                    onClick={() => window.location.reload()}
                    style={{
                      marginTop: '15px',
                      padding: '8px 16px',
                      backgroundColor: '#FFD700',
                      color: '#000',
                      border: 'none',
                      borderRadius: '6px',
                      cursor: 'pointer',
                      fontSize: '14px'
                    }}
                  >
                      
                  </button>
                  <p style={{ fontSize: '12px', color: '#9ca3af', marginTop: '8px' }}>
                          
                  </p>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AITranslation;
